.pets {
    padding-top: 120px;
    background-color: $color-light-l;
    
    @media screen and (max-width: $desktop) {
        padding-bottom: 70px;
    }

    @media screen and (max-width: $tablet) {
        padding-top: 80px;
        padding-bottom: 42px;
    }

    // .pets__container

    &__container {
        padding-top: 80px;
        padding-bottom: 100px;
        display: flex;
        flex-direction: column;
        align-items: center;

        @media screen and (max-width: $desktop) {
            padding-top: 80px;
            padding-bottom: 0;
        }
    }

    // .pets__title

    &__title {
        margin-top: 0;
        margin-bottom: 45px;
        text-align: center;
        color:$color-dark-l;

        @media screen and (max-width: $desktop) {
            margin-bottom: 15px;
        }

        @media screen and (max-width: $tablet) {
            margin-bottom: 25px;
        }
    }

    // .pets__cards

    &__cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 15px 0;
        margin-bottom: 14px;

        @media screen and (max-width: $desktop) {
            justify-content: center;
            column-gap: 40px;
            margin-bottom: 0;
        }
        @media screen and (max-width: $tablet) {
            padding: 20px 0;
            margin-bottom: -10px;
        }
    }

    // .pets__card

    &__card {
        margin-bottom: 30px;

        @media screen and (max-width: $desktop) {
            &:nth-child(7),&:nth-child(8){
                display: none;
            }
        }

        @media screen and (max-width: $tablet) {
            &:nth-child(4),
            &:nth-child(5),
            &:nth-child(6) {
                display: none;
            }
        }
    }


    // .pets__pagination

    // &__pagination {}
}

.pagination {
    display: flex;
    gap:20px;

    @media screen and (max-width: $tablet) {
        gap:10px;
    }

