.friends {
    padding-top:80px;
    padding-bottom: 100px;
    background-color: $color-light-l;

    @media screen and (max-width: $tablet) {
        padding-top:0;
        padding-bottom: 44px;
    }

    // .friends__container

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;

        @media screen and (max-width: $tablet) {
            padding-top: 40px;
        }
    }

    // .friends__slider

    &__slider {
        width: 100%;
        margin-bottom: 60px;

        @media screen and (max-width: $tablet) {
            margin-bottom: 40px;
        }
    }

    // .ftiends__title
    
    &__title {
        text-align: center;
        margin-top: 0;
        margin-bottom: 60px;
        color:$color-dark-l;

        @media screen and (max-width: $tablet) {
            margin-bottom: 43px;
        }
    }
}

.slider {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    @media screen and (max-width: $tablet) {
        flex-wrap: wrap;
        justify-content: space-evenly;
        column-gap: 20px;
    }
    

    // .slider__content
    
    &__content {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        justify-content: center;
        height: 435px;
        overflow: hidden;
        column-gap: 90px;

        @media screen and (max-width: $desktop) {
            column-gap: 40px;
        }

        @media screen and (max-width: $tablet) {
            order: 1;
            margin-bottom: 20px;
        }
    }

    // .slider__prev

    &__prev {
        @media screen and (max-width: $tablet) {
            order: 2;
        }
    }
    

    // .slider__next

    &__next {
        @media screen and (max-width: $tablet) {
            order: 3;
        }
    }
}